import { getLeavesClient } from '@mintlify/http-client';
import { DocsConfig } from '@mintlify/validation';
import type { GetStaticPaths, GetStaticProps } from 'next';
import Head from 'next/head';
import type { ParsedUrlQuery } from 'querystring';
import { useState, KeyboardEvent, useCallback, useEffect } from 'react';

import { DocsConfigContext } from '@/contexts/ConfigContext';
import { getFavicons } from '@/data-fetching/getFavicons';
import FontScript from '@/fonts/FontScript';
import type { FaviconsProps } from '@/types/favicons';
import { ColorVariables } from '@/ui/ColorVariables';
import { ModeToggleScript } from '@/ui/ModeToggleScript';
import { RoundedVariables } from '@/ui/RoundedVariables';
import { cn } from '@/utils/cn';
import { getDetailedFontType } from '@/utils/font/getDetailedFontType';

export default function Login(props: PageProps) {
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState<string>();
  const [isSubmitting, setIsSubmitting] = useState(false);

  const submitPassword = useCallback(async () => {
    setIsSubmitting(true);

    try {
      const res = await fetch('/login/callback/password', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          password: password,
        }),
      });

      if (res.ok) {
        window.location.href = '/';
      } else if (res.status === 401) {
        setErrorMessage('Incorrect password provided.');
      } else {
        setErrorMessage((await res.text()) || 'An unknown error occurred.');
      }
    } finally {
      setIsSubmitting(false);
    }
  }, [password]);

  const handleKeyDown = (event: KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Enter') {
      void submitPassword();
    }
  };

  useEffect(() => {
    if ('redirect' in props) {
      // Account for relative redirects, e.g. for OAuth
      if (props.redirect.startsWith('/')) {
        window.location.href = props.redirect;
      }

      const url = new URL(props.redirect);

      const urlParams = new URLSearchParams(window.location.search);
      const redirect = urlParams.get('redirect');

      if (redirect && typeof redirect === 'string') {
        url.searchParams.append('redirect', redirect);
      }

      window.location.href = url.toString();
    }
  }, [props]);

  if ('redirect' in props) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <SpinnerIcon />
      </div>
    );
  }

  const { docsConfig, favicons } = props;

  return (
    <DocsConfigContext.Provider value={{ docsConfig }}>
      <FontScript />
      <ColorVariables />
      <RoundedVariables />
      <ModeToggleScript />
      <Head>
        <title>{`${docsConfig.name} - Documentation`}</title>
        {favicons.icons.map((favicon) => (
          <link
            rel={favicon.rel}
            type={favicon.type}
            sizes={favicon.sizes}
            href={favicon.href}
            key={favicon.href}
          />
        ))}
      </Head>
      <main
        className={cn(
          'bg-background-light dark:bg-background-dark min-h-screen',
          getDetailedFontType(docsConfig.fonts, 'headings')?.weight &&
            '[[&_:is(h1,h2,h3,h4,h5,h6)]:font-headingsWeight',
          getDetailedFontType(docsConfig.fonts, 'body')?.weight &&
            '[&_*:not(h1,h2,h3,h4,h5,h6,h1_*,h2_*,h3_*,h4_*,h5_*,h6_*)]:font-bodyWeight'
        )}
      >
        <div className="min-h-screen flex items-center justify-center">
          <div className="max-w-xs w-full space-y-8 p-6 bg-white dark:bg-background border border-zinc-200 dark:border-zinc-800 rounded-2xl">
            <div className="text-center flex flex-col gap-2">
              <div className="flex justify-center">
                <img
                  className="max-w-28 max-h-10 mt-1"
                  src={
                    typeof docsConfig.logo === 'string' ? docsConfig.logo : docsConfig.logo?.light
                  }
                  alt="light logo"
                  sizes="100vw"
                />
              </div>
            </div>
            <div className="text-center flex flex-col gap-2">
              <h6 className="text-xl font-medium text-black">Access Restricted</h6>
              <p className="text-sm leading-relaxed text-gray-500">
                To gain access to this doc, provide your access code below.
              </p>
            </div>

            <div className="mt-8 space-y-3">
              <div className="flex flex-col gap-1">
                <div>
                  <label htmlFor="access-code" className="block text-sm font-medium text-black">
                    Enter access code
                  </label>
                  <input
                    id="access-code"
                    name="access-code"
                    type="password"
                    required
                    className={cn(
                      'mt-2 block w-full h-10 px-3 bg-white text-black text-sm py-2 border rounded-lg border-zinc-200 placeholder-text-gray-500 focus:outline-none',
                      errorMessage && 'border-red-600 dark:border-red-600'
                    )}
                    placeholder="Your access code..."
                    value={password}
                    onKeyDown={handleKeyDown}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </div>
                {errorMessage && <div className="text-red-600 text-xs">{errorMessage}</div>}
              </div>
              <div>
                <button
                  type="button"
                  className="w-full flex justify-center items-center h-10 py-2 px-4 rounded-lg text-sm text-white bg-zinc-950/90 hover:bg-zinc-950/80"
                  onClick={submitPassword}
                >
                  {isSubmitting ? <SpinnerIcon /> : 'Access'}
                </button>
              </div>
            </div>
          </div>
        </div>
      </main>
    </DocsConfigContext.Provider>
  );
}

function SpinnerIcon() {
  return (
    <svg
      className="w-4 h-4 text-inherit animate-spin"
      xmlns="http://www.w3.org/2000/svg"
      fill="none"
      viewBox="0 0 24 24"
    >
      <circle
        className="opacity-25"
        cx="12"
        cy="12"
        r="10"
        stroke="currentColor"
        strokeWidth="4"
      ></circle>
      <path
        className="opacity-75"
        fill="currentColor"
        d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
      ></path>
    </svg>
  );
}

interface PathProps extends ParsedUrlQuery {
  subdomain: string;
}

export const getStaticPaths: GetStaticPaths<PathProps> = async () => {
  return {
    paths: [],
    fallback: 'blocking',
  };
};

type PageProps =
  | {
      subdomain: string;
      docsConfig: DocsConfig;
      favicons: FaviconsProps;
    }
  | {
      redirect: string;
    };

export const getStaticProps: GetStaticProps<PageProps, PathProps> = async ({ params }) => {
  if (!params) throw new Error('No path parameters found');
  try {
    const { subdomain: subdomainOrCustomDomain } = params;
    const client = getLeavesClient(subdomainOrCustomDomain);
    const response = await client.getLoginMetadata();

    if ('redirect' in response) {
      return {
        props: {
          redirect: response.redirect,
        },
      };
    }
    const { docsConfig, subdomain } = response;
    const favicons = getFavicons(subdomain);
    return {
      props: {
        subdomain,
        docsConfig,
        favicons,
      },
    };
  } catch (error) {
    console.error('Error fetching login styles:', error);
    return {
      notFound: true,
    };
  }
};
