import { FaviconsProps } from '@/types/favicons';
import { getS3URI } from '@/utils/s3';

const FAVICON_VERSION = 3;

function getFaviconURI(subdomain: string, href: string) {
  return getS3URI(subdomain, `_generated/favicon/${href}?v=${FAVICON_VERSION}`);
}

export function getFavicons(subdomain: string): FaviconsProps {
  return {
    icons: [
      {
        rel: 'apple-touch-icon',
        sizes: '180x180',
        href: getFaviconURI(subdomain, 'apple-touch-icon.png'),
        type: 'image/png',
      },
      {
        rel: 'icon',
        sizes: '32x32',
        href: getFaviconURI(subdomain, 'favicon-32x32.png'),
        type: 'image/png',
      },
      {
        rel: 'icon',
        sizes: '16x16',
        href: getFaviconURI(subdomain, 'favicon-16x16.png'),
        type: 'image/png',
      },
      {
        rel: 'shortcut icon',
        href: getFaviconURI(subdomain, 'favicon.ico'),
        type: 'image/x-icon',
      },
    ],
    browserconfig: getFaviconURI(subdomain, 'browserconfig.xml'),
  };
}
